import { useSearchParams } from 'react-router-dom';
import { motion } from "framer-motion";

import { Welcome } from '.././pages/Welcome';
import { Auth } from '.././pages/Auth';
import { GetCode } from '.././pages/GetCode';

import Box from '@mui/joy/Box';




function Error({ go }) {
    return (
        <div className='box'>
            <h1>ОШИБОЧКА <b>404</b></h1>
            <button onClick={() => { go(""); }}>На главную</button>
        </div>
    );
}



export function Layout() {
    const [searchParams, setSearchParams] = useSearchParams();
    const searchPage = searchParams.get('p') || '';
    var showPage = <Welcome />

    function Animation() {
        return (
            // <motion.div
            //     initial={{ opacity: 0, y: -10 }}
            //     animate={{ opacity: 1, y: 0 }}
            //     transition={{
            //         duration: 0.2,
            //         delay: 0.2,
            //     }}>

            showPage

            // </motion.div>
        );
    }

    function go(to = '') {
        setSearchParams({ p: to });
    }


    if (searchPage === '') {
        showPage = <Welcome searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else if (searchPage === 'auth') {
        showPage = <Auth searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else if (searchPage === 'code-sent') {
        showPage = <GetCode searchParams={searchParams} setSearchParams={setSearchParams} go={go} />
    } else {
        showPage = <Error go={go} />
    }

    return (
        <Box sx={{
            padding: '0px',
            margin: '0px',
        }}>
            <main>
                {/* {showPage} */}
                <Animation />
            </main>
        </Box>
    );

}