import { useState } from 'react';
// import { BiArrowBack } from 'react-icons/bi';


export function Auth({ searchParams, setSearchParams, go }) {
    let [formValue, setFormValue] = useState();

    return (
        <div className='box'>
            <div className="back-box">
                <button className="back-button" onClick={() => {
                    go();
                }}>
                    <span>
                        {/* <BiArrowBack /> */}
                    </span>
                </button>
                <h1>
                    Авторизация
                </h1>
            </div>
            <form onSubmit={(sub) => {
                setSearchParams({ id: sub });
            }}>
                <div className='input-box'>
                    <h2 className='input-h2'>
                        Введите ваш id
                    </h2>
                    <input placeholder='ID' type="number" value={formValue} onChange={val =>
                        setFormValue(val.target.value)
                    }></input>
                </div>

                <div className='but-box'>
                    <button type='submit' onClick={() => {
                        setSearchParams({ p: "code-sent", id: formValue });
                    }}>
                        <span>
                            Подтвердить
                        </span>
                    </button>
                </div>
            </form>
        </div>
    );
}